body {
  background-color: rgba(99, 202, 202, .796);
}

h2 {
  margin: 0;
}

.breed-select {
  min-width: 150px;
  background-color: #f2f2f2;
  border-radius: 5px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 5px;
  font-size: 20px;
}

.cat-info {
  background-color: #c7eaea;
  border: 1px solid #000;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

.cat-card {
  display: none;
}

.js-cat-description {
  max-width: 600px;
}

.js-cat-photo {
  height: 100%;
}

.loader {
  color: red;
  margin-top: 50px;
  font-size: 24px;
}

.error {
  color: red;
  font-size: 24px;
}

.js-loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-right: 10px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.js-loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (min-width: 768px) {
  .cat-info {
    max-width: 900px;
    flex-direction: row;
    margin: 50px;
  }
}

@media screen and (max-width: 767px) {
  .js-cat-photo {
    width: 250px;
  }
}

